.body {
  color: #060b35;
  box-sizing: border-box;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding-inline: 50px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo__img {
  display: block;
}

.nav {
  display: flex;
}

.nav__list {
  align-items: center;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  position: relative;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #060b35;
  text-align: center;
  text-transform: uppercase;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.main {
  justify-content: center;
  align-items: center;
  display: flex;
}

.main__container {
  grid-template-columns: repeat(var(--count-size-monitor), 200px);
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main__container {
    --count-size-monitor: 2;
  }
}

@media (width >= 768px) {
  .main__container {
    --count-size-monitor: 3;
  }
}

@media (width >= 1024px) {
  .main__container {
    --count-size-monitor: 4;
  }
}

.stars {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars .stars__star:last-child {
  margin-right: 0;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.card:hover {
  transition: all .3s;
  transform: scale(1.2);
}

.card__img {
  min-height: 134px;
  margin-bottom: 40px;
  padding-inline: 3px;
}

.card__title {
  color: #060b35;
  margin-top: 0;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__title:hover .cart__title {
  color: #34568b;
  transition: 0, 3s;
}

.card__product-code {
  color: #616070;
  width: 98px;
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__stars {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-top: 0;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin-top: 0;
  margin-bottom: 16px;
  display: flex;
}

.card__name-price {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__num-price {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  outline: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  transition: all .3s;
}
